body {
  margin: 0;
  padding: 0;
  height: 100%;
  background: #051919;
  color: whitesmoke; }

.particles {
  height: 100vh;
  position: fixed;
  right: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1; }

#home {
  display: flex;
  font-size: 5em;
  height: 100vh;
  justify-content: center;
  font-family: 'Ubuntu', sans-serif;
  align-items: center;
  flex-flow: column;
  text-align: center; }
  #home h1 {
    font-size: 14vh; }
  #home h3 {
    font-size: 3vh; }
  #home p {
    font-size: 3vh; }

#links {
  width: 100%;
  margin-top: 2.5%; }

.link {
  margin: 0 2.5%;
  padding: 0.25em 0;
  font-size: 4vh;
  font-family: 'Ubuntu', sans-serif;
  position: relative;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  outline: none;
  color: whitesmoke;
  text-decoration: none; }

.link:hover {
  text-decoration: none;
  cursor: pointer; }

.link::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 0;
  width: 0;
  height: 0.2em;
  opacity: 0;
  background: #6cbc94;
  transition: all 400ms cubic-bezier(0.5, 1.6, 0.15, 0.9); }

.link:hover::after {
  left: 0;
  width: 100%;
  opacity: 1; }

#external_links {
  width: 100%; }

.image_links {
  margin: 0 1%; }
  .image_links img {
    height: 3.5vh;
    opacity: 0.5;
    transition: 0.3s; }

.image_links img:hover {
  opacity: 1;
  -webkit-transform: translateY(-10%);
          transform: translateY(-10%); }

.introduction {
  display: inline-block;
  vertical-align: middle;
  margin: 0 2.5%;
  width: 35%; }
  .introduction h1 {
    font-size: 3.5em;
    text-align: left; }
  .introduction p {
    font-size: 1.5em;
    text-align: left; }

#Picture_Of_Me {
  max-width: 85%;
  height: auto;
  border-radius: 10%;
  box-shadow: 1em 1em 0 rgba(122, 63, 181, 0.5);
  transition: 0.3s; }

#Picture_Of_Me:hover {
  box-shadow: 0.5em 0.5em 0 rgba(122, 63, 181, 0.5); }

#returnHome {
  font-family: "Mrs Sheppards", sans-serif;
  font-size: 2.5em;
  color: whitesmoke;
  text-decoration: none;
  opacity: 0.5;
  transition: 300ms; }

#returnHome:hover {
  opacity: 1; }

li {
  padding: 0.75% 1%;
  display: inline-block;
  font-size: 150%; }

#skills {
  width: 60vw;
